const logInBtn = document.getElementById("log-in-button");
const loginModal = document.getElementById("login-form");
const registerModal = document.getElementById("register-form");
const login = document.getElementById("login");
const register = document.getElementById("register");
const loginModalClose = document.getElementById("login-form-close");
const registerModalClose = document.getElementById("register-form-close");
const lostPasswordModal = document.getElementById("forgotten-password");
const passwordModal = document.getElementById("lost-email");
const lostPasswordModalClose = document.getElementById("missing-email-form-close");


logInBtn.addEventListener("click", () => {
    modalVisible(loginModal)
});

loginModalClose.addEventListener("click", () => {
    modalHide(loginModal);
})

register.addEventListener("click", () => {
    modalHide(loginModal);
    modalVisible(registerModal);
})

login.addEventListener("click", () => {
    modalHide(registerModal);
    modalVisible(loginModal);
})

registerModalClose.addEventListener("click", () => {
    modalHide(loginModal);
    modalHide(registerModal);
})

lostPasswordModal.addEventListener("click", () => {
    modalVisible(passwordModal); 
})

lostPasswordModalClose.addEventListener("click", () => {
    modalHide(passwordModal);
})

function modalVisible(e) {
    e.style.display = "flex";
}

function modalHide(e) {
    e.style.display = "none";
}
const searchBtn = document.getElementById("magnifying-icon");
const inputBar = document.getElementById("input-bar");
const closeBtn = document.getElementById("close-icon");
const accordionTitle = document.getElementById("accordion-title");
const inputBox = document.getElementById("search-bar-box");
const FAQquestions = document.querySelectorAll(".accordion__card");
const searchOutcome = document.getElementById("search-outcome");
const inputText = document.getElementById("input-text");
const notMatch = document.getElementById("no-result");
const FAQquestionsContainer = document.querySelector(".accordion");

searchBtn.addEventListener("click", showInput);
closeBtn.addEventListener("click", hideInput);
closeBtn.addEventListener("click", reset);

function hideInput() {
  inputBar.classList.remove("active");
  accordionTitle.classList.remove("hide-title");
  searchBtn.style.right = "0px";
  inputBox.classList.remove("add-border");
  closeBtn.style.visibility = "hidden";
  inputBar.value = "";
  searchOutcome.style.display = "none";
  inputBar.classList.add("placeholder-hide");
}

function showInput() {
  if (inputBar.classList.contains("active")) {
    inputBar.classList.remove("active");
    accordionTitle.classList.remove("hide-title");
    searchBtn.style.right = "0px";
    inputBox.classList.remove("add-border");
    closeBtn.style.visibility = "hidden";
    inputBar.classList.add("placeholder-hide");
    return;
  }
  inputBar.classList.add("active");
  accordionTitle.classList.add("hide-title");
  searchBtn.style.right = "870px";
  inputBox.classList.add("add-border");
  closeBtn.style.visibility = "visible";
  inputBar.classList.remove("placeholder-hide");
}

function liveSearch() {
  let searchQuery = document.getElementById("input-bar").value;

  for (let i = 0; i < FAQquestions.length; i++) {
    if (
      FAQquestions[i].innerHTML
        .toLowerCase()
        .includes(searchQuery.toLowerCase())
    ) {
      FAQquestions[i].classList.remove("hidden");
      FAQquestions[i].classList.add("accord-drop");
      notMatch.style.display = "none";
      inputText.style.display = "inline";
    } else {
      FAQquestions[i].classList.add("hidden");
      notMatch.style.display = "none";
    }
    if (searchQuery === "") {
      FAQquestions[i].classList.remove("accord-drop");
      searchOutcome.style.display = "none";
      notMatch.style.display = "none";
    } else {
      searchOutcome.style.display = "block";
    }
    if (
      Array.from(FAQquestionsContainer.children).every((child) =>
        child.classList.contains("hidden")
      )
    ) {
      searchOutcome.style.display = "none";
      notMatch.style.display = "block";
    }
    inputText.textContent = inputBar.value;
  }
}

function reset() {
  FAQquestions.forEach((question) => {
    question.classList.remove("hidden", "accord-drop");
  });
  notMatch.style.display = "none";
}

const shopAccordion = document.querySelectorAll(".accordion__header--shop");
const faqAccordion = document.querySelectorAll(".accordion__header--faq");

shopAccordion.forEach(trigger =>
  trigger.addEventListener("click", accordDrop));

faqAccordion.forEach((trigger) =>
  trigger.addEventListener("click", singleAccordDrop));    


function accordDrop() {
  const cards = document.querySelectorAll(".accordion__card");
  const thisCard = this.parentNode;

  cards.forEach((card) => {
    if (thisCard == card) {
      thisCard.classList.toggle("accord-drop");
      return;
    }
  });
}

function singleAccordDrop() {
  const cards = document.querySelectorAll(".accordion__card");
  const thisCard = this.parentNode;

  cards.forEach((card) => {
    if (thisCard == card) {
      thisCard.classList.toggle("accord-drop");
      return;
    }
    card.classList.remove("accord-drop");
  });
}
const elevatorBtn = document.getElementById("elevator");
const pageTop = document.getElementById("top");

elevatorBtn.addEventListener("click", function() {
    pageTop.scrollIntoView({ behavior: "smooth"});
})